.btn {
    display: inline-block;
    text-align: center;
    text-decoration: none;

    margin: 2px 0;

    border: solid 1px transparent;
    border-radius: 4px;

    padding: 0.5em 1em;

    color: #ffffff;
    background-color: #9555af;
}

.btn:active {
    transform: translateY(1px);
    filter: saturate(150%);
}

.btn:hover,
.btn:focus {
    color: #9555af;
    border-color: currentColor;
    background-color: white;
}